@import url(https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap);
body {
    margin: 0;
}

.top-bar {
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
    padding-bottom: 15px;
    width: 100%;
    position:fixed;
    top:0;
    z-index:100;
    box-shadow: 0 4px 15px -5px #e0e0e0;
}

.top-text {
    text-align: center;
    margin: 5px auto 5px;
    font-size: 2rem;
    font-family: 'Lora', serif;
}

.sankey-wrapper {
    position: relative;
    margin-top: 10rem; 
    width: 100%;
    height: calc(100vh - 10rem);
}

.sankey {
    display: block;
    width: 75%;
    height: calc(100vh - 10rem);
    max-height: 1200px;
    margin: 0 auto;
}

.scale-loader {
    position: absolute;
    top: 40%;
    left: 48%;
    display: block;
    margin: 0 auto;
}

.no-data {
    color: slategray;
    width: 100%;
    text-align: center;
    position: absolute;
    top: 40%;
    display: block;
    margin: 0 auto
}

@media screen and (min-width: 768px) {
   .language-search {
        display: inline-block;
        margin-left: 1rem;
        width: 16rem;
    }
   .article-search {
        display: inline-block;
        margin-left: 0.5rem;
        margin-right: 1rem;
        width: calc(100% - 18.5rem);
   } 
   
}

@media screen and (max-width: 767px) {
   .sankey {
        width: 100%;
        height: 900px;
   }
   .article-search, .language-search {
        padding-left:5px;
        padding-right: 5px;
   }
   .article-search {
        padding-top: 12px;
   }
}








